<template>
  <div>
    <loading :active.sync="isLoading"></loading>

    <button class="btn btn-outline-info mt-3" @click="openModel(true)"></button>

    <table class="table table-hover mt-4">
      <thead>
        <tr>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"> % </th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"> / </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in coupons" :key="item.id" class="item-hover">
          <th scope="row">{{ item.title }}</th>
          <td>{{ item.code }}</td>
          <td>{{ item.percent }}</td>
          <!-- filterstimestampdue_date, main.js -->
          <td>{{ item.due_date | timestamp }}</td>
          <td>
            <b class="text-success" v-if="item.is_enabled"></b>
            <b class="text-danger" v-else></b>
          </td>
          <td>
            <button class="btn btn-outline-primary mr-1" @click="openModel(false, item)"></button>
            <!-- id -->
            <button class="btn btn-outline-secondary" @click="deletCoupon(item.id)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!--  -->
    <div class="modal fade" id="modal-add" tabindex="-1" role="dialog" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered modal-lg" role="document">
        <form class="modal-content" @submit.prevent="updateCoupon()">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLongTitle"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12 mt-2">
                <label></label>
                <input
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model="tempData.title"
                  v-validate="'required'"
                  name="coupon_name"
                >
                <span class="text-danger" v-if="errors.has('coupon_name')"></span>
              </div>
              <div class="col-md-6 mt-2">
                <label>(%)</label>
                <input
                  type="text"
                  class="form-control"
                  placeholder=" 80 "
                  v-model="tempData.percent"
                  v-validate="'required'"
                  name="coupon_percent"
                >
                <span class="text-danger" v-if="errors.has('coupon_percent')"></span>
              </div>
              <div class="col-md-6 mt-2">
                <div class="form-group">
                  <label></label>
                  <input
                    type="date"
                    class="form-control"
                    :class="{'is-invalid': errors.has('end-date')}"
                    name="end-date"
                    v-model="due_date"
                    v-validate="'required'"
                  >
                  <span class="text-danger" v-if="errors.has('end-date')"></span>
                </div>
              </div>
              <div class="col-md-12 mt-2">
                <div class="form-group">
                  <label for="coupon-code"></label>
                  <input
                    type="text"
                    class="form-control"
                    :class="{'is-invalid': errors.has('coupon-code')}"
                    name="coupon-code"
                    id="coupon-code"
                    placeholder=""
                    v-model="tempData.code"
                    v-validate="'required'"
                  >
                  <span class="text-danger" v-if="errors.has('coupon-code')"></span>
                </div>
              </div>
              <div class="col-md-12 mt-3">
                <label>
                  <input type="checkbox" value="0" v-model="tempData.is_enabled"> /
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button type="submit" class="btn btn-primary"></button>
          </div>
        </form>
      </div>
    </div>
    <!--  -->
    <!-- <pagination>main.jsVue.component('pagination', Pages) -->
    <!--  -->
    <!-- :page-data="pagination"Pagination.vuepageDatapagination{} -->
    <pagination :page-data="pagination" @changepage="getCoupon"></pagination>
    <!-- $emit("changepage", page) -->
  </div>
</template>

<script>
import $ from "jquery";
export default {
  data() {
    return {
      coupons: [],
      pagination: {},
      // (put)(delete)this.tempData = Object.assign({}, item);item{},item & tempData
      tempData: {
        title: "",
        percent: 100,
        code: ""
      },
      due_date: "",
      isLoading: false,
      isNew: false
    };
  },
  watch: {
    // due_data,
    due_date() {
      const vm = this;
      // ()
      const timestamp = Math.floor(new Date(vm.due_date) / 1000);
      // tempDatadue_date
      vm.tempData.due_date = timestamp;
    }
  },
  methods: {
    getCoupon(page = 1) {
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E5%8F%96%E5%BE%97%E5%84%AA%E6%83%A0%E5%88%B8%E5%88%97%E8%A1%A8
      // 
      const SERVER_PATH = "https://vue-course-api.hexschool.io";
      const API_PATH = "caris";
      const api = `${SERVER_PATH}/api/${API_PATH}/admin/coupons?page=${page}`;
      const vm = this;

      vm.isLoading = true;

      this.$http.get(api).then(response => {
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
        console.log("getCoupon", response);
      });
    },
    openModel(isNew, item) {
      const vm = this;
      if (isNew) {
        this.tempData = {}; // 
        this.due_date = "";
        this.isNew = true; // isNew=true ()
      } else {
        // 
        vm.due_date = new Date(item.due_date * 1000)
          .toISOString()
          .split("T")[0];
        // Tdate & time,

        // console.log(
        //   "before split",
        //   new Date(item.due_date * 1000).toISOString()
        // );
        // console.log("after split", vm.due_date);

        // https://jigsawye.com/2015/10/06/javascript-object-assign/
        this.tempData = Object.assign({}, item); // 
        // ,tempDataisNew==truetempData,ES6
        // item{}this.tempData,item & tempData
        this.isNew = false; // isNew=false ()
      }
      $("#modal-add").modal("show");
    },
    updateCoupon() {
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E6%96%B0%E5%A2%9E%E5%84%AA%E6%83%A0%E5%88%B8
      // 
      let SERVER_PATH = "https://vue-course-api.hexschool.io";
      let API_PATH = "caris";
      let api = `${SERVER_PATH}/api/${API_PATH}/admin/coupon`;
      let httpMethods = "post";
      const vm = this;

      // api()
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E4%BF%AE%E6%94%B9%E5%84%AA%E6%83%A0%E5%88%B8
      if (!vm.isNew) {
        //apiconsterror,var or let
        api = `${SERVER_PATH}/api/${API_PATH}/admin/coupon/${vm.tempData.id}`;
        httpMethods = "put";
      }

      vm.isLoading = true;
      // console.log("updateCoupon-vm.tempData", vm.tempData); //openModel

      // ,
      this.$validator.validate().then(result => {
        // (id)
        if (result) {
          // ,{}data:{},data:{}
          // vm.tempData/admin/coupon/data()
          // /admin/coupon/:id/data()
          this.$http[httpMethods](api, { data: vm.tempData }).then(response => {
            if (response.data.success) {
              // 
              $("#modal-add").modal("hide");
              vm.getCoupon();
              // console.log("$validator-success", response);
            } else {
              $("#modal-add").modal("hide");
              vm.getCoupon();
              console.log("", response.data);
            }
            vm.isLoading = false;
          });
        } else {
          console.log("");
          vm.isLoading = false;
        }
      });
    },
    deletCoupon(coupon_id) {
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E7%AE%A1%E7%90%86%E6%8E%A7%E5%88%B6%E5%8F%B0-%5B%E9%9C%80%E9%A9%97%E8%AD%89%5D#%E5%88%AA%E9%99%A4%E5%84%AA%E6%83%A0%E5%88%B8
      // 
      let SERVER_PATH = "https://vue-course-api.hexschool.io";
      let API_PATH = "caris";
      let api = `${SERVER_PATH}/api/${API_PATH}/admin/coupon/${coupon_id}`;
      const vm = this;
      const d = confirm("?");

      if (d) {
        this.$http.delete(api).then(response => {
          vm.getCoupon();
          console.log("deletCoupon", response.data);
        });
      }
    }
  },
  created() {
    this.getCoupon();
  }
};
</script>
<style lang="scss" scoped>
.item-hover:hover {
  background-color: darken(white, 10%);
}
</style>


