<template>
  <div class="row justify-content-center" style="margin-top:150px">
    <loading :active.sync="isLoading"></loading>
    <!--  -->
    <p v-if="cart.carts.length > 0" style="font-size:30px"></p>
    <p v-else style="font-size:30px;"></p>
    <table v-if="cart.carts.length > 0" class="table col-md-12">
      <thead>
        <tr>
          <th></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in cart.carts" :key="item.id">
          <td style="text-align:center">
            <button
              @click="removeCartItem(item.id,item.product.title)"
              class="btn btn-outline-danger btn-sm"
            >
              <i class="far fa-trash-alt"></i>
            </button>
          </td>
          <td>
            {{item.product.title}}
            <div class="text-success" v-if="item.coupon"></div>
          </td>
          <td>{{item.qty}}/{{item.product.unit}}</td>
          <td>{{item.total}}</td>
        </tr>
      </tbody>
      <tfoot>
        <tr>
          <td colspan="3" class="text-right"></td>
          <td class="text-right">{{cart.total}}</td>
        </tr>
        <tr v-if="cart.final_total !== cart.total">
          <td colspan="3" class="text-right text-success"></td>
          <td class="text-right text-success">{{cart.final_total}}</td>
        </tr>
      </tfoot>
    </table>
    <!--  -->
    <!-- submit,createOrder -->
    <form class="col-md-12" style="margin-bottom:40px" @submit.prevent="createOrder">
      <div class="form-group">
        <!-- email https://github.com/baianat/vee-validate -->
        <label for="useremail">Email</label>
        <!-- requirederrorchrome -->
        <!-- email -->
        <input
          type="email"
          class="form-control"
          name="email"
          id="useremail"
          v-model="form.user.email"
          placeholder=" Email"
          v-validate="'required|email'"
          required
        >
        <span class="text-danger" v-if="errors.has('email')">
          <!-- v-validateemailerrors.first() -->
          {{errors.first('email')}}
        </span>
      </div>

      <div class="form-group">
        <label for="username"></label>
        <!-- :classerrors.has()true,input -->
        <input
          type="text"
          class="form-control"
          :class="{'is-invalid': errors.has('u_name')}"
          name="u_name"
          id="username"
          v-model="form.user.name"
          placeholder=""
          v-validate="'required'"
        >
        <!-- errors.has()validate -->
        <!-- v-validate="required",true -->
        <span class="text-danger" v-if="errors.has('u_name')"></span>
      </div>

      <div class="form-group">
        <label for="usertel"></label>
        <input
          type="tel"
          class="form-control"
          :class="{'is-invalid': errors.has('tel')}"
          name="tel"
          id="usertel"
          v-model="form.user.tel"
          placeholder=""
          v-validate="'required'"
        >
        <span class="text-danger" v-if="errors.has('tel')"></span>
      </div>

      <div class="form-group">
        <label for="useraddress"></label>
        <input
          type="address"
          class="form-control"
          :class="{'is-invalid': errors.has('address')}"
          name="address"
          id="useraddress"
          v-model="form.user.address"
          placeholder=""
          v-validate="'required'"
        >
        <span class="text-danger" v-if="errors.has('address')"></span>
      </div>
      <div class="input-group mb-3 input-group-sm">
        <input v-model="coupon_code" type="text" class="form-control" placeholder="">
        <div class="input-group-append">
          <button
            v-tip.keep="item.content"
            v-for="(item, index) in toolTip_data"
            :key="index"
            @click.prevent="choiceCoupon(item.percent)"
            type="button"
            class="btn btn-outline-secondary"
          >{{item.title}}</button>
          <button
            style="background:blue;color:white"
            @click.prevent="useCouponCode"
            type="button"
            class="btn btn-outline-secondary"
          ></button>
        </div>
      </div>
      <div class="form-group">
        <label for="useraddress"></label>
        <textarea name id class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
      </div>
      <div class="text-right">
        <button class="btn btn-danger"></button>
      </div>
    </form>
  </div>
</template>
<script>
// alert("JS");
import $ from "jquery";
import { mapState, mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      // showCart: [], store => cart: {carts: []},
      coupon_code: "",
      // <input>v-model
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: ""
        },
        message: ""
      },
      toolTip_data: [
        {
          title: " 5 ",
          content: "50,0005",
          percent: "50%"
        },
        {
          title: " 7 ",
          content: "30,0007",
          percent: "70%"
        },
        {
          title: " 9 ",
          content: "20,0009",
          percent: "90%"
        }
      ]

      /*
      store
      total: {
        original: 0,
        final: 0
      }*/
    };
  },
  methods: {
    ...mapActions("cartModules", ["getCart"]),
    // store
    /*
    getCart() {
      const SERVER_PATH = "https://vue-course-api.hexschool.io";
      const API_PATH = "caris";
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E5%AE%A2%E6%88%B6%E8%B3%BC%E7%89%A9-%5B%E5%85%8D%E9%A9%97%E8%AD%89%5D#%E5%8F%96%E5%BE%97%E8%B3%BC%E7%89%A9%E8%BB%8A%E5%88%97%E8%A1%A8
      // ,
      const api = `${SERVER_PATH}/api/${API_PATH}/cart`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        // console.log("getCart(showCart)", response);
        vm.showCart = response.data.data.carts;
        vm.total.original = response.data.data.total;
        vm.total.final = response.data.data.final_total;
        vm.isLoading = false;
      });
    },*/
    removeCartItem(id, title) {
      const vm = this;
      vm.$store.dispatch("cartModules/removeCartItem", { id, title }); // dispatch()actions
      /*
      store
      const SERVER_PATH = "https://vue-course-api.hexschool.io";
      const API_PATH = "caris";
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E5%AE%A2%E6%88%B6%E8%B3%BC%E7%89%A9-%5B%E5%85%8D%E9%A9%97%E8%AD%89%5D#%E5%88%AA%E9%99%A4%E6%9F%90%E4%B8%80%E7%AD%86%E8%B3%BC%E7%89%A9%E8%BB%8A%E8%B3%87%E6%96%99
      // ,
      const api = `${SERVER_PATH}/api/${API_PATH}/cart/${id}`;
      const vm = this;
      var r = confirm("?");
      vm.isLoading = true;
      if (r) {
        this.$http.delete(api).then(response => {
          // console.log("removeCartItem", response);
          this.getCart();
          vm.isLoading = false;
        });
      } else {
        vm.isLoading = false;
      }*/
    },
    choiceCoupon(percent) {
      const vm = this;
      if (percent === "50%") {
        if (vm.cart.total >= 50000) {
          vm.coupon_code = "555";
        } else {
          alert("50,000!");
        }
      } else if (percent === "70%") {
        if (vm.cart.total >= 30000) {
          vm.coupon_code = "777";
        } else {
          alert("30,000!");
        }
      } else if (percent === "90%") {
        if (vm.cart.total >= 20000) {
          vm.coupon_code = "999";
        } else {
          alert("20,000!");
        }
      }
    },
    useCouponCode() {
      const api = `${process.env.SERVER_API_PATH}/api/${
        process.env.USER_PATH
      }/coupon`;
      const vm = this;
      // API
      const coupon = {
        code: vm.coupon_code
      };
      vm.$store.dispatch("cartModules/updateLoading", true);
      // https://github.com/hexschool/vue-course-api-wiki/wiki/%E5%AE%A2%E6%88%B6%E8%B3%BC%E7%89%A9-[%E5%85%8D%E9%A9%97%E8%AD%89]#%E5%A5%97%E7%94%A8%E5%84%AA%E6%83%A0%E5%88%B8
      // ,coupon
      this.$http.post(api, { data: coupon }).then(response => {
        console.log("Cart-useCouponCode", response.data.success);
        if (response.data.success) {
          vm.$bus.$emit(
            "msg-from-cart-coupon",
            response.data.message,
            "primary"
          );
          vm.$store.dispatch("cartModules/getCart");
          vm.$store.dispatch("cartModules/updateLoading", false);
        } else {
          vm.$bus.$emit(
            "msg-from-cart-coupon",
            response.data.message,
            "danger"
          );
          vm.$store.dispatch("cartModules/updateLoading", false);
        }
      });
    },
    createOrder() {
      const api = `${process.env.SERVER_API_PATH}/api/${
        process.env.USER_PATH
      }/order`;
      const vm = this;
      const order = vm.form; // <form><input>v-modelpost
      this.$store.dispatch("cartModules/updateLoading", true);
      // ,
      this.$validator.validate().then(result => {
        // 
        if (result) {
          // https://github.com/hexschool/vue-course-api-wiki/wiki/%E5%AE%A2%E6%88%B6%E8%B3%BC%E7%89%A9-[%E5%85%8D%E9%A9%97%E8%AD%89]#%E7%B5%90%E5%B8%B3%E9%A0%81%E9%9D%A2
          // ,order
          this.$http.post(api, { data: order }).then(response => {
            console.log("createOrder-", response);
            if (response.data.success) {
              // vm.showCart = []; // API(customer_checkout),
              this.$store.dispatch("cartModules/updateLoading", false);
              vm.$router.push(`/customer_checkout/${response.data.orderId}`);
            }
          });
        } else {
          this.$store.dispatch("cartModules/updateLoading", false);
          console.log("");
        }
      });
    }
  },
  created() {
    this.getCart();
  },
  computed: {
    ...mapGetters("cartModules", ["isLoading", "cart"])
  }
};
</script>
<style lang="scss" scope>
</style>


